body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: sans-serif;
}

.wrap {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  position: relative;
}

h1.absolute-label {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  font-size: 75px;
  transition: ease .4s;
  mix-blend-mode: color-burn;
  font-family: 'Righteous', cursive;

  transform: translate(-50%, -100%);
}
.left-side {
  width: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  transition: linear 0.4s;
  opacity: 0.9;
}
.left-wrap {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 25px 50px;
}
.left-wrap_columns {
  display: flex;
  width: 80%;
  align-items: center;
  justify-content: space-around;
}
.column {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 1.4;
}
.column b {
  text-transform: capitalize;
  margin: 5px 0;
}
.hide {
  transition: linear 0.4s;
  width: 0 !important;
  opacity: 0;
}

.right-side {
  width: 50%;
  overflow-y: scroll;
  height: 100%;
  transition: linear 0.4s;
  display: flex;
  flex-direction: column;
}

section {
  background: #ccc;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
}

.hidden-section {
  background: #000;
  width: 50%;
  transition: linear 0.4s;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.fixed {
  position: absolute;
  height: 100vh;
  width: 50%;
  z-index: 555;
}

section#one {
  background: linear-gradient(#1e130c, #9a8478);
}
section#two {
  background: linear-gradient(#9a8478, #948e99);
}
section#three {
  background: linear-gradient(#948e99, #2e1437);
}
section#four {
  background: linear-gradient(#2e1437, #360033);
}
section#five {
  background: linear-gradient(#360033, #0b8793);
}
section.locked {
  background: linear-gradient(#0b8793, #948e99);
}
